.catalog__wrapper {
  padding: 27px 0 20px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.catalog__title {
  @include title;
  text-transform: none;
  line-height: 36px;
  margin: 0 auto 41px;
  width: calc(100% - 40px);
  max-width: 510px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin: 53px auto 83px;
    width: calc(100% - 60px);
    max-width: 900px;
  }

  @media (min-width: $desktop-width) {
    margin: 43px auto 83px;
    max-width: 1220px;
  }
}
