.common-button {
  background-color: $basic-green;
  color: $basic-white;
  text-align: center;
  display: block;
  border: none;

  @include regular-text;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;
  margin: 0;
  text-decoration: none;
  text-transform: uppercase;
  box-sizing: border-box;
}

.common-button:hover {
  background-color: $basic-green-2;
}

.common-button:active {
  background-color: $basic-green-2;
  color: $basic-white-active;
}
